<template>
  <div class="about">
    <div class="minWidth">
      <all_titel :indexText="indexText"></all_titel>
      <div class="productsTitle">
        <div class="productsText">
          精选系列：
        </div>
        <div class="navList">
          <ul>
            <li @click="checkNav(2)" :class="[addClassToBg===2 ? activeClass : '']">精选龙头</li>
            <li @click="checkNav(3)" :class="[addClassToBg===3 ? activeClass : '']">精选五金</li>
            <li @click="checkNav(4)" :class="[addClassToBg===4 ? activeClass : '']">精选智能</li>
            <li @click="checkNav(5)" :class="[addClassToBg===5 ? activeClass : '']">精选定制</li>
          </ul>
        </div>
      </div>
      <div class="pc-pro">
        <div class="proList">
          <ul>
            <li  class="list-item" v-for="item in activeData" :key="item.id" @click="jumpToDetails(item.id)">
              <div class="listImg"><img :src=item.url alt=""></div>
              <div class="listText">{{item.name}}</div>
            </li>
          </ul>
        </div>
        <div class="pageBottom">
          <pagePay @clickPage="clickPage" :nun="num"></pagePay>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import all_titel from '@/components/pageNavigation/pageNavigation'
  import pagePay from '@/components/pagingButton/pagingButton'
  export default {
    name: "aboutIndex",
    props:{
      proList:{
        type:Object
      }
    },
    components:{
      all_titel,
      pagePay
    },
    data(){
      return{
        num:1,
        addClassToBg:1,
        activeClass:'baColor',
        solutionNav:'',
        selectData:{},//精选产品总数据
        activeData:[],//当前数据
      }
    },
    computed:{
      solution_nav(){
        return this.$store.state.solution_nav;
      },
      indexText(){
        return this.$store.state.indexText;
      }
    },
    created(){
      this.selectData = this.proList.selectedData;
      this.listenForNav();
    },
    watch:{
      solution_nav(){
        let solutionNav = this.$store.state.solution_nav;
        if(solutionNav===31){
          this.addClassToBg =2;
          this.activeData = this.selectData.faucetData;
        }else if(solutionNav===32){
          this.addClassToBg =3;
          this.activeData = this.selectData.IntelligenceData;
        }else if(solutionNav===33){
          this.addClassToBg =4;
          this.activeData = this.selectData.dressingData;
        }else if(solutionNav===34){
          this.addClassToBg =5;
          this.activeData = this.selectData.customData;
        }
      }
    },
    methods:{
      clickPage(val){
        if(this.num===2){
          if(val===1){
          }else if(val===2){
          }
        }
      },
      listenForNav (){
        this.solutionNav = this.$store.state.solution_nav;
        if(this.solutionNav===''){
          this.addClassToBg =2;
          this.activeData = this.selectData.faucetData;
        }else if(this.solutionNav===31){
          this.addClassToBg =2;
          this.activeData = this.selectData.faucetData;
        }else if(this.solutionNav===32){
          this.addClassToBg =3;
          this.activeData = this.selectData.IntelligenceData;
        }else if(this.solutionNav===33){
          this.addClassToBg =4;
          this.activeData = this.selectData.dressingData;
        }else if(this.solutionNav===34){
          this.addClassToBg =5;
          this.activeData = this.selectData.customData;
        }
      },
      checkNav(val){
       if(val===2){
         this.activeData = this.selectData.faucetData;
          this.$store.dispatch('listenTOAllTitle',['首页','精选系列','精选龙头']);
          this.$store.dispatch('listenTOsecondNav',31);
          this.addClassToBg = val;
        }else if(val===3){
         this.activeData = this.selectData.IntelligenceData;
          this.$store.dispatch('listenTOAllTitle',['首页','精选系列','精选五金']);
          this.$store.dispatch('listenTOsecondNav',32);
          this.addClassToBg = val;
        }else if(val===4){
         this.activeData = this.selectData.dressingData;
          this.$store.dispatch('listenTOAllTitle',['首页','精选系列','精选智能']);
          this.$store.dispatch('listenTOsecondNav',33);
          this.addClassToBg = val;
        }else if(val===5){
         this.activeData = this.selectData.customData;
          this.$store.dispatch('listenTOAllTitle',['首页','精选系列','精选定制']);
          this.$store.dispatch('listenTOsecondNav',34);
          this.addClassToBg = val;
        }
      },
      jumpToDetails(val){
        this.$router.push({
          path: '/details',
          query: {
            id: val
          }
        });
        $('body,html').animate({
          scrollTop:0
        },500)
      }
    }
  }
</script>

<style scoped>
  .about{
    width: 100%;
    margin:10px auto 0 auto;
    overflow: hidden;
  }
  .minWidth {
    width: 960px;
    margin: 0 auto;
  }

  /*二级导航栏*/
  .productsTitle{
    width: 100%;
    height: 40px;
    overflow: hidden;
    margin-top: 40px;
  }
  .productsText{
    width: 10%;
    font-weight: bold;
    background-color: #e5e5e5;
    text-align: center;
    float: left;
    line-height: 40px;
  }
  .navList{
    width: 90%;
    line-height: 40px;
    background-color: #fafafa;
    float: left;
    overflow: hidden;
  }
  .navList ul {
    width: 60%;
    overflow: hidden;
    display: flex;
  }
  .navList ul li{
    flex: 1;
    text-align: center;
    cursor: pointer;
    margin-right: 10px;
    font-size: 16px;
    color: #999;
  }
  .navList ul li:hover{
    color: #4fb1f7 !important;
  }
  .baColor{
    color: #4fb1f7 !important;
  }

/*产平列表*/
.proList{
  width: 100%;
  margin-top: 30px;
  overflow: hidden;
}
.list-item{
  width: 230px;
  float: left;
  overflow: hidden;
  border: 1px solid #f4f4f4;
  margin-right: 12px;
  margin-bottom: 20px;
  box-sizing: border-box;
  cursor: pointer;
}
.list-item:nth-child(4n){
  margin-right: 0;
}
.listImg{
  width: 100%;
  overflow: hidden;
}
.listImg img{
  width: 230px;
  height: 230px;
  display: block;
  transition: all .6s;
}
.list-item:hover img{
  transform: scale(1.4);
}
.list-item:hover{
  border: 1px solid saddlebrown;
  box-sizing: border-box;
}
.listText{
  width: 100%;
  padding: 10px 0;
  font-size: 14px;
  color: #666666;
  text-align: center;
  background-color: #f4f4f4;
}
.pageBottom{
  margin: 18px 0 30px 0;
}
ul {
  list-style-type: none;
  padding: 0;
}
  @media screen and (max-width: 768px) {
    .minWidth {
      width: 94%;
      margin:  0 auto;
    }
    .productsTitle{
      width: 100%;
      height: 40px;
      overflow: hidden;
      margin-top: 20px;
    }
    .productsText{
      width: 20%;
      font-weight: bold;
      font-size: 12px;
      background-color: #e5e5e5;
      text-align: center;
      float: left;
      line-height: 40px;
    }
    .navList{
      width: 80%;
      line-height: 40px;
      background-color: #fafafa;
      float: left;
      overflow: hidden;
    }
    .navList ul {
      width:100%;
      overflow: hidden;
      display: flex;
    }
    .navList ul li{
      font-size: 12px;
    }
    .proList{
      width: 100%;
      margin-top: 30px;
      overflow: hidden;
    }
    .list-item{
      width: 49%;
      float: left;
      overflow: hidden;
      border: 1px solid #f4f4f4;
      margin-right:2%;
      margin-bottom: 10px;
      box-sizing: border-box;
      cursor: pointer;
    }
    .list-item:nth-child(2n){
      margin-right: 0;
    }
    .listImg{
      width: 100%;
      height: 172px;
      overflow: hidden;
    }
    .listImg img{
      width: 100%;
      height: 100%;
      display: block;
    }
    .listText{
      width: 100%;
      padding: 10px 0;
      font-size: 12px;
      color: #666666;
      text-align: center;
      background-color: #f4f4f4;
    }
    .pageBottom{
      margin: 18px 0 30px 0;
    }
  }
</style>



