<template>
  <div class="buttonPage">
    <span class="page" @click="pageNext(1)">上一页</span><span class="nun">{{num1}}/{{num2}}</span><span class="page" @click="pageNext(2)">下一页</span>
  </div>
</template>

<script>
    export default {
      name: "pagingButton",
      props:['nun'],
      data(){
         return{
           num1:1 ,//当前页嘛
         }
      },
      computed:{
        num2(){
          return this.nun
        }
      },
      watch:{
        num2(val, oldVal){
          this.num1=1;
        }
      },
      methods:{
        pageNext(val){
          if(val===1){
            if(this.num1!==1){
              $('body,html').animate({
                scrollTop:300
              },200);
              this.num1 --
            }
          }else{
            if(this.num1!==this.num2){
              $('body,html').animate({
                scrollTop:300
              },200);
              this.num1 ++
            }
          }
          this.$emit('clickPage',val)
        }
      }

    }
</script>

<style scoped>
  .buttonPage{
    width: 210px;
    margin: 0px auto;
  }
  .page{
    width: 30%;
    padding: 8px 0;
    text-align: center;
    overflow: hidden;
    display: inline-block;
    border: 1px solid #ccc;
    box-sizing: border-box;
    cursor: pointer;
  }
  .nun{
    padding: 8px 0;
    text-align: center;
    width: 30%;
    color: white;
    overflow: hidden;
    background: #69bcfb;
    display: inline-block;
    border: 1px solid #69bcfb;
    box-sizing: border-box;
  }
  @media screen and (max-width: 768px){
    .buttonPage{
      width: 180px;
      margin: 0 auto;
    }
    .page{
      width: 70px;
      height: 30px;
      line-height: 30px;
      padding: 0;

    }
    .nun{
      padding: 0;
      height: 30px;
      line-height: 30px;
      width: 40px;
    }
  }
</style>



